import React, { Component } from 'react';
import { Grid, Typography, ButtonBase, Button } from '@material-ui/core';
import styled, { keyframes } from 'styled-components'
import NavBar from './NavBar'
import Proyectos from './Proyectos'
import Experiencia from './Experiencia'
import Contacto from './Contacto'
import AnimatedButton from './AnimatedButton'

import CodeOutlined from '@material-ui/icons/CodeOutlined';
import ClassOutlined from '@material-ui/icons/ClassOutlined';
import SmsOutlined from '@material-ui/icons/SmsOutlined';

const aparecerAnimation = keyframes`
  from {
    opacity:0;
  }
  to {
    opacity:1;
  }
`
const LandingAnimatedButton = styled(AnimatedButton) `
  opacity:0;
  animation: ${aparecerAnimation} 3s ease;
  animation-delay: ${props => props.delay ? props.delay : 0}s;
  animation-fill-mode: forwards; 
`

const LandingLeft = styled.div`
  position:absolute;
  bottom:${props => !props.isMobile ? 0 : props.focusOnRight ? '85%' : '10%'};
  right:${props => props.isMobile ? 0 : props.focusOnRight ? '85%' : '15%'};
  width: ${props => props.isMobile ? '100%' : '85%'};
  transition: all 1s;
  height: 100%;
  overflow: hidden;
  background: #000000;
  background: -webkit-linear-gradient(to left, #000000, #0b060a, #0e0d15, #0c141c, #041a21, #042025, #042528, #072b2a);
  background: linear-gradient(to left, #000000, #0b060a, #0e0d15, #0c141c, #041a21, #042025, #042528, #072b2a);
 `
const LandingRight = styled.div`
  position:absolute;
  bottom:0;
  right:0;
  width: ${props => props.isMobile ? '100%' : props.focusOnRight ? '85%' : '15%'};
  top: ${props => !props.isMobile ? 0 : !props.focusOnRight ? '85%' : '10%'};
  transition: all 1s;
  height: 100%;
  overflow: hidden;
  background: #fff;
 `
const ImgDiv = styled.div`
  position: absolute; 
  top: ${props => (props.screenHeight - (props.isMobile ? props.screenWidth * 0.7 : props.screenWidth * 0.85 * 0.7)) / 2}px;
  left: ${props => props.isMobile ? props.screenWidth * 0.15 : props.screenWidth * 0.85 * 0.15}px;
  width: ${props => props.isMobile ? props.screenWidth * 0.7 : props.screenWidth * 0.85 * 0.7}px; 
  height: ${props => props.isMobile ? props.screenWidth * 0.7 : props.screenWidth * 0.85 * 0.7}px;
  border-radius:50%;
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 220px -27px rgba(0,0,0,0);
  -moz-box-shadow: 0px 0px 220px -27px rgba(0,0,0,0);
  box-shadow: 0px 0px 220px -27px rgba(0,0,0,0);
  animation:
  ${ keyframes`
    from {
      opacity:0;
      transform:rotateY(0deg);
    }
    to {
      opacity:1
      transform:rotateY(360deg);
    }
  `} 2s ease;
  animation-delay: 0s;
  animation-fill-mode: forwards; 
`
const NombreLine = styled.div`
  position:absolute;
  top:65%;
  left:10%;
  width:0;
  height:2px;
  background-color:#fff;
  animation:
  ${ keyframes`
    from {
      width:0;
      opacity:0;
    }
    to {
      width:80%;
      opacity:1;
    }
  `} 3s ease,
  ${props => props.hoverOnImg ?
    keyframes`
    0% {
      left:10%;
      width:80%;
      opacity:1;
    }
    50% {
      left:50%
      width:0;
      opacity:0;
    }
    100% {
      left:10%
      width:80%;
      opacity:1;
    }`
    : null} 0.5s ease-in-out;
  animation-delay: 1s, 0s;
  animation-fill-mode: forwards, none; 
`
const NombreDivOuter = styled.div`
  position:absolute;
  top:65%;
  left:10%;
  height:100px;
  width:80%;
  overflow:hidden;
`
const NombreDivInner = styled.div`
  position:absolute;
  bottom:100%;
  width:100%;
  text-align: center;
  vertical-align: middle;
  line-height: 100px;       
  animation:
  ${ keyframes`
    from {
      opacity:0;
      top:-15%;
    }
    to {
      opacity:1;
      top:15%;
    }
  `} 2s ease;
  animation-delay: 2s;
  animation-fill-mode: forwards; 
`
const NombreDivOuterSubtitulo = styled.div`
  position:absolute;
  bottom:35%;
  left:10%;
  height:100px;
  width:80%;
  overflow:hidden;
`
const NombreDivInnerSubtitulo = styled.div`
  position:absolute;
  bottom:0%;
  width:100%;
  text-align: center;
  vertical-align: middle;
  line-height: 100px;    
  opacity:0;   
  animation:
  ${props => props.hoverOnImg ?
    keyframes`
      from {
        opacity:0;
        bottom:-15%;
      }
      to {
        opacity:1;
        bottom:14%;
      }
    `
    : null
  } 2s ease;
  animation-fill-mode: forwards; 
`
const SelectAnimation = styled.div`
height: 40px;
width: 40px;
border - radius: 50 %;
border - color: #2b2b2b;
border - style: solid;
border - width: 1px 2px 3px 4px;
text - align: center;
vertical - align: middle;
line - height: 40px;
`
const AnimCardImg = styled.img`{
  background - position: center;
  background - size: cover;
  position: absolute;
  transition: all 0.3s ease;
    &: hover {
    opacity: 0.4;
    transform: scale(1.3);
  }
} `

class LandingPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      section: 0,
      focusOnRight: false,
      hoverOnImg: false

    };
  }

  onMouseEnterRight() {
    this.setState({ focusOnRight: true })
  }

  onMouseLeaveRight() {
    this.setState({ focusOnRight: false })
  }

  onButtonPress(index) {
    this.setState({ section: index })
  }

  onImgHover() {
    this.setState({ hoverOnImg: true })
  }

  onImgHoverOut() {
    this.setState({ hoverOnImg: false })
  }

  render() {
    return (
      <div style={{
        width: '100%', height: '100%', overflow: 'hidden'
      }}>
        <LandingLeft focusOnRight={this.state.focusOnRight} isMobile={this.props.isMobile}>
          <ImgDiv screenWidth={this.props.screenWidth} screenHeight={this.props.screenHeight} isMobile={this.props.isMobile} onMouseEnter={() => this.onImgHover()} onMouseLeave={() => this.onImgHoverOut()}>
            <AnimCardImg src={require('../assets/IMG-20170409-WA0023-round.jpg')} alt='Leandro Ercoli'
              style={{ maxHeight: '100%', maxWidth: '100%', marginLeft: 'auto', marginRight: 'auto', display: 'block' }} />
          </ImgDiv>
          <NombreLine hoverOnImg={this.state.hoverOnImg} />

          <NombreDivOuter>
            <NombreDivInner width={this.props.isMobile ? this.props.screenWidth : this.props.screenWidth * 0.7}>
              <Typography variant="h3" component="h3" style={{ fontSize: this.props.isMobile ? 36 : this.state.hoverOnImg ? 48 : 42, color: '#fff', fontWeight: 200, transition: 'font-size 0.5s' }}>LEANDRO ERCOLI</Typography>
            </NombreDivInner>
          </NombreDivOuter>
          <NombreDivOuterSubtitulo>
            <NombreDivInnerSubtitulo width={this.props.isMobile ? this.props.screenWidth : this.props.screenWidth * 0.7} hoverOnImg={this.state.hoverOnImg}>
              <Typography variant="h3" component="h3" style={{ fontSize: this.props.isMobile ? 26 : 32, color: '#fff', fontWeight: 200 }}>Ingeniero en Sistemas - Desarrollador web y móvil</Typography>
            </NombreDivInnerSubtitulo>
          </NombreDivOuterSubtitulo>

        </LandingLeft>
        <LandingRight focusOnRight={this.state.focusOnRight} isMobile={this.props.isMobile} onMouseEnter={() => this.onMouseEnterRight()} onMouseLeave={() => this.onMouseLeaveRight()} >

          <NavBar focus={this.state.focusOnRight} section={this.state.section} isMobile={this.props.isMobile} onButtonPress={(index) => this.onButtonPress(index)} />
          {
            !this.props.isMobile && !this.state.focusOnRight ?
              null
              : <div style={{ position: 'absolute', top: '10%', width: '100%', height: this.props.isMobile ? '80%' : '90%' }}>
                {this.state.section == 0 ?
                  <Experiencia
                    isMobile={this.props.isMobile}
                    focusOnRight={this.state.focusOnRight}
                    screenWidth={this.props.isMobile ? this.props.screenWidth : this.state.focusOnRight ? this.props.screenWidth * 0.85 : this.props.screenWidth * 0.3}
                    screenHeight={this.props.isMobile ? this.state.focusOnRight ? this.props.screenHeight * 0.8 : 0 : this.props.screenHeight * 0.9} />
                  : this.state.section == 1 ?
                    <Proyectos
                      isMobile={this.props.isMobile}
                      focusOnRight={this.state.focusOnRight}
                      screenWidth={this.props.isMobile ? this.props.screenWidth : this.state.focusOnRight ? this.props.screenWidth * 0.85 : this.props.screenWidth * 0.3}
                      screenHeight={this.props.isMobile ? this.state.focusOnRight ? this.props.screenHeight * 0.8 : 0 : this.props.screenHeight * 0.9} />
                    : this.state.section == 2 ?
                      <Contacto
                        isMobile={this.props.isMobile}
                        focusOnRight={this.state.focusOnRight}
                        screenWidth={this.props.isMobile ? this.props.screenWidth : this.state.focusOnRight ? this.props.screenWidth * 0.85 : this.props.screenWidth * 0.3}
                        screenHeight={this.props.isMobile ? this.state.focusOnRight ? this.props.screenHeight * 0.8 : 0 : this.props.screenHeight * 0.9} />
                      : null
                }
              </div>
          }

        </LandingRight>
      </div >
    )

  }
}

export default LandingPage;
