export default class Proyectos extends Component {
  constructor(props) {
    super(props);

    this.cardWidth = this.props.screenWidth * 0.7 // le resto el tamaño del landingleft
    this.innerMargin = 15
    this.scrollLeftMargin = this.props.screenWidth * 0.15 - this.innerMargin

    this.state = {
      focusedElement: 1,
      hoveredContent: null
    };
  }

  scrollTo(fromIndex, toIndex, fastVelocity) {
    let targetScroll =
      toIndex == 1 ? 0
        : this.scrollLeftMargin + this.innerMargin
        + (this.cardWidth * 0.8) + this.innerMargin
        + (this.cardWidth + 2 * this.innerMargin) * (toIndex - 2)

    const forward = this.container.scrollLeft < targetScroll

    clearInterval(this.timerNext);
    this.timerNext = setInterval(
      () => {
        if (forward && this.container.scrollLeft <= targetScroll
          || !forward && this.container.scrollLeft > targetScroll) {
          this.container.scrollLeft += (fastVelocity ? 6 : 3) * Math.sign(toIndex - fromIndex);
        } else {
          clearInterval(this.timerNext);
        }
      }, 3)
  }


  handleFocus(index, fastVelocity) {
    if (index != this.state.focusedElement) {
      this.scrollTo(this.state.focusedElement, index, fastVelocity)
      this.setState({ focusedElement: index })
    }
  }

  componentWillUnmount() {
    clearInterval(this.timerPrev);
    clearInterval(this.timerNext);
  };

  onHoverContent(index) {
    this.setState({ hoveredContent: index })
  }
  onHoverOutContent() {
    this.setState({ hoveredContent: null })
  }

  render() {
    return (
      <div
        style={{ width: '100%', height: '100%', overflow: 'hidden', margin: 0 }}>
        <Grid item style={{ width: '100%', height: '80%', padding: 0, overflow: 'hidden', position: 'absolute', top: '5%' }}>
          <Scroller ref={el => this.container = el} style={{
            overflow: 'hidden',
            whiteSpace: 'nowrap',
            height: '100%',
            paddingLeft: this.scrollLeftMargin,
            paddingRight: this.scrollLeftMargin
          }} paddingLeft={this.scrollLeftMargin}>
            {
              list.map((proy, index) => (
                <AnimatedCard
                  key={index}
                  focusedElement={(this.state.focusedElement && this.state.focusedElement == index + 1) ? true : false}
                  onMouseEnter={() => this.handleFocus(index + 1, false)}
                  cardWidth={this.cardWidth}
                  innerMargin={this.innerMargin}
                  onMouseEnter={() => this.onHoverContent(index)}
                  onMouseLeave={() => this.onHoverOutContent()}>
                  <div style={{ 
                    position: 'absolute', width: '100%', height: '100%', overflow: 'hidden', borderRadius: '10px', backgroundColor: '#2b2b2b' }}>
                    <AnimCardImg img={proy.img} hoveredContent={this.state.hoveredContent == index}/>
                  </div>
                  <div style={{
                    width: this.props.isMobile ? '80%' : this.state.hoveredContent == index ? '80%' : '40%',
                    height: '70%',
                    position: 'absolute',
                    top: '15%',
                    left: this.props.isMobile ? '10%' : this.state.hoveredContent == index ? '10%' : '5%',
                    backgroundColor: 'rgba(255,255,255,0.8)',
                    padding: '15px 10px',
                    transition: 'width 0.5s',
                    background:'#000000',
                    background:'-webkit-linear-gradient(to bottom, rgba(0,0,0,1), rgba(11, 6, 10, 0.9), rgba(14, 13, 21, 0.8), rgba(12, 20, 28, 0.8), rgba(4, 26, 33, 0.8), rgba(4, 32, 37, 0.7), rgba(4, 37, 40, 0.6), rgba(7, 43, 42, 0.5))',
                    background:'linear-gradient(to bottom, rgba(0,0,0,1), rgba(11, 6, 10, 0.9), rgba(14, 13, 21, 0.8), rgba(12, 20, 28, 0.8), rgba(4, 26, 33, 0.8), rgba(4, 32, 37, 0.7), rgba(4, 37, 40, 0.6), rgba(7, 43, 42, 0.5))'
   
                  }} >
                    <Grid item container direction='column' justify='flex-start' alignItems='center' style={{ width: '100%', height: '100%' }}>
                      <Typography variant="h1" style={{ fontSize: '1.5em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'center' }}>{proy.titulo.toUpperCase()}</Typography>
                      {
                        this.props.focusOnRight ?
                          <div style={{ marginTop: '5%', height: '70%', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                            <div style={{ overflowY: 'auto' }}>
                              <Typography variant="subtitle1" style={{ fontSize: '1em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'left' }}>{proy.descripcion}</Typography>
                            </div>
                          </div>
                          : null
                      }
                    </Grid>
                  </div>

                  <Hashtags >
                    {
                      proy.hashtags.map((tag, indesx) => (
                        <Hashtag key={"" + index + tag}><Typography variant="subtitle1" style={{ fontSize: '0.9em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'center' }}>{tag}</Typography></Hashtag>
                      ))
                    }
                  </Hashtags>

                  {
                    proy.linkCodigo != "" ?
                      <BottomIcon left>
                        <a href={proy.linkCodigo} target="_blank" rel="noopener noreferrer" ><CodeOutlined style={{ color: '#2b2b2b', fontSize: 28 }} /></a>
                      </BottomIcon>
                      : null
                  }
                  {
                    proy.linkPagina != "" ?
                      <BottomIcon>
                        <a href={proy.linkPagina} target="_blank" rel="noopener noreferrer" ><LaptopMac style={{ color: '#2b2b2b', fontSize: 28 }} /></a>
                      </BottomIcon>
                      : null
                  }


                </AnimatedCard>
              ))
            }
          </Scroller>

        </Grid>

        <div style={{ backgroundColor: 'transparent', width: '100%', height: '10%', position: 'absolute', bottom: this.props.isMobile ? '10%' : 0 }}>
          <Stepper listLength={list.length} onSelect={(index) => this.handleFocus(index + 1, true)} selected={this.state.focusedElement - 1} />
        </div>
      </div>
    )
  }
}

Proyectos.propTypes = {
  isMobile: PropTypes.bool.isRequired
};
Proyectos.defaultProps = {
};
